import React, { PureComponent, Suspense } from "react";
import Masonry from 'react-masonry-component';
import { isTagShop } from '../../../actions/api'
import "./scss/ClassicPhoto.scss";
const Card = React.lazy(() => import('./Elements/Card'));

const ClassicPhotoExtend = ({ Posts, completeDataObject, clickToShowPopUp, adjustWidth, onClickToCTA, onClickPopUpSlider, CardStyle, ThemeStyle, ThemeInfo }) => {
  return ((Posts && Posts.length > 0)) && Posts.map((item, index) => {
    const Post = completeDataObject[item];
    return <Suspense fallback={<></>}><Card key={index} adjustWidth={adjustWidth} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} Post={Post} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle}  ThemeInfo={ThemeInfo}/></Suspense>
  })
}

class ClassicPhoto extends PureComponent {

  render() {
    const { postData, completeDataObject, adjustWidth, CardStyle, ThemeStyle, ThemeInfo, clickToShowPopUp, onClickToCTA, onClickPopUpSlider } = this.props;

    return isTagShop ? <Masonry
      className={`tb_cp_post_container`}
      tabIndex='0'
      aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
      elementType={'div'}
      options={{
        transitionDuration: 0,
        percentPosition: true,
        itemSelector: '.tb_cp_post_wrapper',
        horizontalOrder: true,
        resize: true,
      }}
      disableImagesLoaded={false}
      updateOnEachImageLoad={false}
      imagesLoadedOptions={{}}
      enableResizableChildren={true}>
      <ClassicPhotoExtend clickToShowPopUp={clickToShowPopUp} Posts={postData} completeDataObject={completeDataObject} adjustWidth={adjustWidth} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} />
    </Masonry>
    : 
    <div className="tb_cp_post_container">
      <ClassicPhotoExtend clickToShowPopUp={clickToShowPopUp} Posts={postData} completeDataObject={completeDataObject} adjustWidth={adjustWidth} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} />
    </div>
  }
}

export default ClassicPhoto


